ground-color:#0d324d;background-color:var(--secondary-color);background-image:-webkit-linear-gradient(45deg, var(--primary-color) 25%, transparent 25%, transparent 50%, var(--primary-color) 50%, var(--primary-color) 75%, transparent 75%, transparent)}#logo{text-align:center;padding:2em}#logo h2{font-style:italic}img{max-width:100%}#main{text-align:center;padding:2em;min-height:50vh;background-repeat:no-repeat;background-position:center}#copyright{text-align:center;padding-top:2em;margin-top:6em;border-top:solid 1px rgba(255, 255, 255, 0.1);border-top:solid 1px var(--border-color)}#copyright ul{display:inline-block;border-radius:0.35em;box-shadow:inset 0px 0px 1px 1px rgba(255, 255, 255, 0.1);box-shadow:inset 0px 0px 1px 1px var(--border-color);color:#aaa;color:rgba(255,255,255,0.25);padding:0.75em 2em;font-size:0.9em}#copyright ul li{display:inline-block;margin-left:1em;padding-left:1em;border-left:solid 1px #333;border-left-color:rgba(255, 255, 255, 0.1);border-left-color:var(--border-color)}#copyright ul li:first-child{border-left:0;margin-left:0;padding-left:0}#copyright a{color:inherit}#copyright a:hover{color:#fff}

